.container {
    height: 4.75rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--color-main-yellow);
    padding:.5rem 0;
}
.title{
    flex-basis:60%;
    height: 100%;
}
.title i{
    background-image: url(../../images/title.png);
    width: 100%;
    height: 100%;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50%;
}